import React from "react";
import Spinner from "react-bootstrap/Spinner";

function Loader() {
  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background: "black",
      }}
    >
      <Spinner animation="border" variant="primary" />
    </div>
  );
}

export default Loader;
