import React from "react";
import { Route, Switch } from "react-router-dom";
import Auth from "../hoc/auth";
// pages for this product
// import LandingPage from "./views/LandingPage/LandingPage.js";
// import LoginPage from "./views/LoginPage/LoginPage.js";
// import RegisterPage from "./views/RegisterPage/RegisterPage.js";
import Navigation from "./views/Nav/Nav";
import Footer from "./views/Footer/Footer";
import Loader from "./Loader/Loader";
// import FavoritePage from "./views/FavoritePage/FavoritePage";
// import MovieVideo from "./views/MovieDetail/MovieVideo";

const LandingPage = React.lazy(() => import("./views/LandingPage/LandingPage"));
const LoginPage = React.lazy(() => import("./views/LoginPage/LoginPage"));
const RegisterPage = React.lazy(() =>
  import("./views/RegisterPage/RegisterPage")
);
const MovieVideo = React.lazy(() => import("./views/MovieDetail/MovieVideo"));
const FavoritePage = React.lazy(() =>
  import("./views/FavoritePage/FavoritePage")
);

function App() {
  return (
    <React.Suspense fallback={<Loader />}>
      {" "}
      <Navigation />
      <Switch>
        <Route exact path="/" component={Auth(LandingPage, null)} />
        <Route path="/login" component={Auth(LoginPage, false)} />
        <Route path="/register" component={Auth(RegisterPage, false)} />
        <Route path="/movie/:id" component={Auth(MovieVideo, null)} />
        <Route path="/favorite" component={Auth(FavoritePage, null)} />
      </Switch>{" "}
      <Footer />
    </React.Suspense>
  );
}

export default App;
