import axios from "axios";

//SERVER ROUTES

export const USER_SERVER = "/api/users";

export const API_URL = "https://api.themoviedb.org/3/";
export const API_KEY = "844dba0bfd8f3a4f3799f6130ef9e335";
export const apiKey = "844dba0bfd8f3a4f3799f6130ef9e335";
export const nowPlayingUrl = `${API_URL}movie/now_playing`;
export const topratedUrl = `${API_URL}movie/top_rated`;
export const movieUrl = `${API_URL}movie`;
export const genreUrl = `${API_URL}genre/movie/list`;
export const moviesUrl = `${API_URL}discover/movie`;
export const personUrl = `${API_URL}trending/person/week`;

export const IMAGE_BASE_URL = "http://image.tmdb.org/t/p/";

//Sizes: w300, w780, w1280, original
export const BACKDROP_SIZE = "w1280";
export const IMAGE_SIZE = "w1280";

// w92, w154, w185, w342, w500, w780, original
export const POSTER_SIZE = "w185";

export const fetchMovies = async () => {
  try {
    const { data } = await axios.get(nowPlayingUrl, {
      params: {
        api_key: apiKey,
        language: "en_US",
        page: 1,
      },
    });
    // console.log(data);
    const posterUrl = "https://image.tmdb.org/t/p/original/";
    const modifiedData = data["results"].map((m) => ({
      id: m["id"],
      backPoster: posterUrl + m["backdrop_path"],
      popularity: m["popularity"],
      title: m["title"],
      poster: posterUrl + m["poster_path"],
      overview: m["overview"],
      rating: m["vote_average"],
      release: m["release_date"],
    }));
    // console.log(modifiedData);
    return modifiedData;
  } catch (error) {}
};

export const fetchGenre = async () => {
  try {
    const { data } = await axios.get(genreUrl, {
      params: {
        api_key: apiKey,
        language: "en_US",
        page: 1,
      },
    });

    const modifiedData = data["genres"].map((g) => ({
      id: g["id"],
      name: g["name"],
    }));
    return modifiedData;
  } catch (error) {}
};

export const fetchMovieByGenre = async (genre_id) => {
  try {
    const { data } = await axios.get(moviesUrl, {
      params: {
        api_key: apiKey,
        language: "en_US",
        page: 1,
        with_genres: genre_id,
      },
    });
    const posterUrl = "https://image.tmdb.org/t/p/w200/";
    const modifiedData = data["results"].map((m) => ({
      id: m["id"],
      backPoster: posterUrl + m["backdrop_path"],
      popularity: m["popularity"],
      title: m["title"],
      poster: posterUrl + m["poster_path"],
      overview: m["overview"],
      rating: m["vote_average"],
    }));
    return modifiedData;
  } catch (error) {}
};

